import { environment } from './../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ExamsService {

  constructor(private http: HttpClient) { }

  getExames(){
    return this.http.get<any>(`${environment.api_local}/exames`)
  }

  getGroupExam(id: number){
    return this.http.get<any>(`${environment.api_local}/exames/grupo/${id}`)
  }

  getPerfilMetabolicoUser(month: number){
    return this.http.get<any>(`${environment.api_local}/users/exams/perfil-metabolico/${month}`)
  }
  getPerfilMetabolicoUserAll(){
    return this.http.get<any>(`${environment.api_local}/users/exams/perfil-metabolico-historico`)
  }
  insertPerfilMetabolicoUser(exams){
    return this.http.post<any>(`${environment.api_local}/users/exams/perfil-metabolico-user/`,exams)
  }

  getPerfilHormonalUser(month: number){
    return this.http.get<any>(`${environment.api_local}/users/exams/perfil-hormonal/${month}`)
  }
  getPerfilHormonalUserAll(){
    return this.http.get<any>(`${environment.api_local}/users/exams/perfil-hormonal-historico`)
  }
  insertPerfilHormonalUser(exams){
    return this.http.post<any>(`${environment.api_local}/users/exams/perfil-hormonal-user`,exams)
  }
}
