import { environment } from './../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Subject, Observable } from 'rxjs';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class DietService {
  observableDiaSemana: Subject<any> = new Subject()
  constructor(private http: HttpClient) { }
  setObservableDiaSemana(value: any){
    this.observableDiaSemana.next(value)
  }

  getObservableDiaSemana(): Observable<any>{
    return this.observableDiaSemana.asObservable();
  }

  getDietUserDay(dados): Observable<any>{
    return this.http.post<any>(`${environment.api_local}/dietas/dia`,dados)
  }
}
