import { environment } from './../../environments/environment';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ExercicioService {

  constructor(private http: HttpClient) { }

  getTreinoUserDay(dados): Observable<any>{
    return this.http.post<any>(`${environment.api_local}/treinos/dia`,dados)
  }

  getItemTreino(id):Observable<any>{
    return this.http.get<any>(`${environment.api_local}/itens-treinos/${id}`)
  }
}
