import { UserImc } from './../models/user_imc.model';
import { Observable, Subject, throwError} from 'rxjs';
import { HttpClient,HttpErrorResponse } from '@angular/common/http';
import { catchError } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  observableWeight: Subject<any> = new Subject()

  constructor(private http: HttpClient) { }

  insertWeight(weight): Observable<any>{
    return this.http.post<any>(`${environment.api_local}/usuarios/peso`, weight).pipe(
      catchError((error: HttpErrorResponse) => {
        let errorMessage = '';
        if (error.error instanceof ErrorEvent) {
          errorMessage = `Erro: ${error.error.message}`;
        } else {
          errorMessage = `${error.error.message}`;
        }
        return throwError(errorMessage);
      })
    );
  }

  insertHeight(height): Observable<any>{
    return this.http.post<any>(`${environment.api_local}/usuarios/altura`, height).pipe(
      catchError((error: HttpErrorResponse) => {
        let errorMessage = '';
        if (error.error instanceof ErrorEvent) {
          errorMessage = `Erro: ${error.error.message}`;
        } else {
          errorMessage = `${error.error.message}`;
        }
        return throwError(errorMessage);
      })
    );;
  }

  insertImc(imc: any): Observable<any>{
    return this.http.post<any>(`${environment.api_local}/usuarios/imc`, imc);
  }

  setObservableWeight(value: any){
    this.observableWeight.next(value)
  }

  getObservableWeight(): Observable<any>{
    return this.observableWeight.asObservable();
  }

  getUserImc(){
    return this.http.get<any>(`${environment.api_local}/usuarios/tipo-imc`);
  }

  insertGoalsUser(goals): Observable<any> {
    return this.http.post<any>(`${environment.api_local}/usuarios/metas`,goals)
  }

  insertPhysicalUser(exercises): Observable<any>{
    return this.http.post<any>(`${environment.api_local}/usuarios/frequencia-atividade`,exercises)   
  }

  insertIllinessUser(illiness): Observable<any>{
    return this.http.post<any>(`${environment.api_local}/usuarios/doencas`,illiness)
  }
  insertPlanUser(user): Observable<any>{
    return this.http.post<any>(`${environment.api_local}/usuarios/programa`,user)
  }
  insertTimeTrainig(training): Observable<any>{
    return this.http.post<any>(`${environment.api_local}/usuarios/tempo-treino`,training).pipe(
      catchError((error: HttpErrorResponse) => {
        let errorMessage = '';
        if (error.error instanceof ErrorEvent) {
          errorMessage = `Erro: ${error.error.message}`;
        } else {
          errorMessage = `${error.error.message}`;
        }
        return throwError(errorMessage);
      })
    );
  }
  insertUserSleep(sleep): Observable<any>{
    return this.http.post<any>(`${environment.api_local}/usuarios/sono`,sleep).pipe(
      catchError((error: HttpErrorResponse) => {
        let errorMessage = '';
        if (error.error instanceof ErrorEvent) {
          errorMessage = `Erro: ${error.error.message}`;
        } else {
          errorMessage = `${error.error.message}`;
        }
        return throwError(errorMessage);
      })
    );
  }
  getDietUser(): Observable<any>{
    return this.http.get<any>(`${environment.api_local}/users/diet`)
  }

  getWeightUser(){
    return this.http.get<any>(`${environment.api_local}/usuarios/peso`)
  }
  getHeightUser(){
    return this.http.get<any>(`${environment.api_local}/usuarios/altura`)
  }
  getGoalsUser(){
    return this.http.get<any>(`${environment.api_local}/usuarios/metas`)
  }

  getTreinosRealizados(){
    return this.http.get<any>(`${environment.api_local}/usuarios/treinos-realizados`);
  }

  getSleepUser(){
    return this.http.get<any>(`${environment.api_local}/usuarios/sono/dia`);
  }

  insertHidroUser(hidro){
    return this.http.post<any>(`${environment.api_local}/usuarios/hidratacao`, hidro)
  }
  getHidroUser(){
    return this.http.get<any>(`${environment.api_local}/usuarios/hidratacao/dia`)
  }

  getUserPlan(){
    return this.http.get<any>(`${environment.api_local}/usuarios/tipo-programa`)
  }

  insertExameUser(exame): Observable<any>{
    return this.http.post<any>(`${environment.api_local}/usuarios/exame`,exame).pipe(
      catchError((error: HttpErrorResponse) => {
        let errorMessage = '';
        if (error.error instanceof ErrorEvent) {
          errorMessage = `Erro: ${error.error.message}`;
        } else {
          errorMessage = `${error.error.message}`;
        }
        return throwError(errorMessage);
      })
    );
  }

  historicoUserExames(grupo): Observable<any>{
    return this.http.post<any>(`${environment.api_local}/usuarios/historico/exames`,grupo);
  }
  utlimosExames(grupo): Observable<any>{
    return this.http.post<any>(`${environment.api_local}/usuarios/historico/ultimos-exames`,grupo);
  }
 
}
